import React, { Component } from 'react';
import { Link, Router } from 'react-router-dom';
import './App.css';
import LogoutButton from './components/Logout';
import { withFirebase } from './components/Firebase';
import AuthUserContext from './components/Session/context';

class Navbar extends Component {
constructor(props) {
    super(props);    
    this.ref = this.props.firebase.firestored();
    this.users = this.props.firebase.users();
    this.unsubscribe = null;
    this.state = {
    posts: [],
    authUser: null,
    email: ""
    };
}

onCollectionUpdate = (querySnapshot) => {
    const posts = [];
    querySnapshot.forEach((doc) => {
    const { title, description, author, category } = doc.data();
    posts.push({
        key: doc.id,
        doc, // DocumentSnapshot
        title,
        description,
        author,
        category,
    });
    });
    this.setState({
    posts
    });
}

componentDidMount() {
    this.unsubscribe = this.ref.onSnapshot(this.onCollectionUpdate);

    this.listener = this.props.firebase.auth.onAuthStateChanged(
    authUser => {
        authUser ? this.setState({ authUser}) : this.setState({ authUser: null }); 
    },
    );
}

componentWillUnmount() {
    this.listener();
}

render() {
    return (                  
    <AuthUserContext.Provider value={this.state.authUser, this.users}>
        <div>
        <AuthUserContext.Consumer>
            {this.state.authUser, user =>{              
            if(this.state.authUser && (user.currentUser.email == "someone@gmail.com"))
            {
                return <this.NavigationAdmin/>
            }else if(this.state.authUser)
            {
                return <this.NavigationAuth/>
            }else
            {
                return <this.NavigationNonAuth/>
            };
            }
            }            
        </AuthUserContext.Consumer>
        <br></br><br></br> <br></br> <br></br>
        </div>
    </AuthUserContext.Provider>          
    );
}

NavigationAdmin= () => (    
    <div>    
    <nav class="navbar navbar-expand-lg navbar-light bg-light fixed-top">
        <div class="container">
            <div class="navbar-brand">
            <Link to="/"><img alt="Inicio Multi Wiki" src={require('./LogoWordSmall.png')} height="45px"></img></Link>                          
            </div>
            <button class="navbar-toggler border-danger" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarSupportedContent">
            <ul class="navbar-nav mr-auto"></ul>                                       
            <Link to="/showcategory" class="nav-link"><b>Categorías</b></Link>
            <Link to="/Createcategory" class="nav-link"><b>Nueva categoria</b></Link>
            <Link to="/create" class="nav-link"><b>Nuevo Post</b></Link>              
            <div class="nav-item dropdown">
                <Link class="nav-link dropdown-toggle" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"><b>{this.users.currentUser.email}</b>&nbsp;&nbsp;<img class="imgUsuario" alt="Usuario" src={require('./Usuario.png')} height="30px"></img></Link>
                <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                <Link to="/Passwordchange" class="dropdown-item nav-link"><img class="imgUsuario" alt="Cambiar contraseña" src={require('./Configuracion.png')} height="20px"></img>&nbsp;&nbsp;<b>Cambiar contraseña</b></Link>
                <LogoutButton/>                  
                </div>
            </div>
            </div>
        </div>
    </nav>    
</div>
);


NavigationAuth = () => (      
    <div>      
    <nav class="navbar navbar-expand-lg navbar-light bg-light fixed-top">
        <div class="container">
        <div class="navbar-brand">
            <Link to="/"><img alt="Inicio Multi Wiki" src={require('./LogoWordSmall.png')} height="45px"></img></Link>                          
            </div>
            <button class="navbar-toggler border-danger" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarSupportedContent">
            <ul class="navbar-nav mr-auto"></ul>                                       
            <Link to="/showcategory" class="nav-link"><b>Categorías</b></Link>
            <Link to="/create" class="nav-link"><b>Nuevo Post</b></Link>              
            <div class="nav-item dropdown">
                <Link class="nav-link dropdown-toggle" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"><b>{this.users.currentUser.email}</b>&nbsp;&nbsp;<img class="imgUsuario" alt="Usuario" src={require('./Usuario.png')} height="30px"></img></Link>
                <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                <Link to="/Passwordchange" class="dropdown-item nav-link"><img class="imgUsuario" alt="Cambiar contraseña" src={require('./Configuracion.png')} height="20px"></img>&nbsp;&nbsp;<b>Cambiar contraseña</b></Link>
                <LogoutButton/>                  
                </div>
            </div>
            </div>
        </div>
    </nav>    
    </div>   
);

NavigationNonAuth = () => (      
    <div>                
        <nav class="navbar navbar-expand-lg navbar-light bg-light fixed-top">
        <div class="container">
            <div class="navbar-brand">
            <Link to="/"><img alt="Inicio Multi Wiki" src={require('./LogoWordSmall.png')} height="45px"></img></Link>                          
            </div>
            <button class="navbar-toggler border-danger" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarSupportedContent">
            <ul class="navbar-nav mr-auto"></ul>                                       
            <Link to="/Login" class="nav-link"><b>Iniciar Sesión</b></Link>
            <Link to="/showcategory" class="nav-link"><b>Categorías</b></Link>
            </div>
        </div>
        </nav>        
    </div>      
);  
}

export default withFirebase(Navbar);
