import React from 'react';
import { withFirebase } from './Firebase';
import { Link, Router } from 'react-router-dom';

const SignOutButton = ({ firebase }) => (  
  <Link class="dropdown-item nav-link" onClick={firebase.doSignOut}><img class="imgUsuario" alt="Cerrar Sesión" src={require('../Cerrar.png')} height="20px"></img>&nbsp;&nbsp;<b>Cerrar Sesión</b></Link>
);

export default withFirebase(SignOutButton);