import React, { Component } from 'react';
import './App.css';
import List from './components/Searchbar';
import { withFirebase } from './components/Firebase';
import Navbar from './navbar';

class App extends Component {
  constructor(props) {
    super(props);    
    this.ref = this.props.firebase.firestored();
    this.users = this.props.firebase.users();
    this.unsubscribe = null;
    this.state = {
      posts: [],
      authUser: null,
      email: ""
    };
  }

  onCollectionUpdate = (querySnapshot) => {
    const posts = [];
    querySnapshot.forEach((doc) => {
      const { title, description, author, category } = doc.data();
      posts.push({
        key: doc.id,
        doc, // DocumentSnapshot
        title,
        description,
        author,
        category,
      });
    });
    this.setState({
      posts
    });
  }

  componentDidMount() {
    this.unsubscribe = this.ref.onSnapshot(this.onCollectionUpdate);

    this.listener = this.props.firebase.auth.onAuthStateChanged(
      authUser => {
        authUser ? this.setState({ authUser}) : this.setState({ authUser: null }); 
      },
    );
  }

  componentWillUnmount() {
    this.listener();
  }

  render() {
    return (
      <div>         
        <Navbar/>      
        <div class="container">        
          <List items={this.state.posts} />
          {/* <div class="container">
            <button type="button" class="btn btn-primary mt-5" data-toggle="collapse" data-target="#demo">Leer mas</button>
            <div id="demo" class="collapse">
              Lorem ipsum dolor sit amet, consectetur adipisicing elit,
              sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,
              quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.
            </div>
          </div> */}
        </div>
      </div>                   
    );
  }  
}

export default withFirebase(App);
